/* Fonts */
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@100;200;300;400;500;600;700&display=swap');
/* --- */

:root {
  --main-bg-color: #fff;
  --highlight-bg-color: #0000ff;
  --dark-bg-color: #000;
  --main-text-color: #000;
  --secondary-text-color: #ccc;
  --footer-secondary-text-color: #ccc;
  --highlight-text-color: #0000ff;
  --main-font: 'Barlow', sans-serif;

  --normal-font-size: 20px;
  --normal-font-weight: 500;
}

[data-theme="dark"] {
  --main-bg-color: #000;
  --highlight-bg-color: #0000ff;
  --dark-bg-color: #fff;
  --main-text-color: #fff;
  --secondary-text-color: #ccc;
  --footer-secondary-text-color: #333;
  --highlight-text-color: #3697fc;
  --main-font: 'Barlow', sans-serif;

  --normal-font-size: 20px;
  --normal-font-weight: 500;
}

body {
  margin: 0;
  font-family: var(--main-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--main-bg-color);
}

/* Nav */
header{
  position: fixed;
  height: 80%;
}

.mode-switch{
  color: var(--main-text-color);
  cursor: pointer;
}

nav a:hover{
  text-decoration: none;
}

nav span{
  margin: 1rem 0px;
  display: block;
  color: var(--secondary-text-color);
  font-size: var(--normal-font-size);
  font-weight: var(--normal-font-weight);
  cursor: pointer;
  user-select: none;
  transition: 0.2s all;

}

nav span:hover, nav span.active{
  color: var(--highlight-text-color);
}
/* Nav */

/* Footer */
footer{
  background-color: var(--dark-bg-color);
  position: fixed;
  bottom: 0px;
  width: 100%;
  padding: 1rem 0px;
}

footer a, footer a:hover{
  color: var(--footer-secondary-text-color);
  font-size: var(--normal-font-size);
  font-weight: var(--normal-font-weight);
  user-select: none;
}
/* Footer */

.section-container{
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Home */
#home h1{
  background-color: var(--highlight-bg-color);
  color: #fff;
  display: inline-block;
  padding: 3px 2rem;
  font-size: 6rem;
}

#home h2{
  color: var(--main-text-color);
  font-size: 4rem;
}
/* Home */

.bottom-spacer{
  height: 6rem;
}

/* Scroll */
.scroll-track{
  position: fixed;
  height: 80%;
}

.scroll-btn{
  height: 60px;
  width: 20px;
  background-color: var(--dark-bg-color);
  border-radius: 100px;
  position: absolute;
  cursor: grab;
  top: 688px;
}

.scroll-btn.active{
  cursor: grabbing;
}
/* Scroll */

/* About */
#about p{
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 40px;
  color: var(--main-text-color);
}
/* About */